nav {
  display: flex;
  justify-content: center;
  width: 95vw;
  position: fixed;
  background-color: white;
 z-index: 1;
  padding-top: .5rem;
  padding-bottom: 1rem;
}

ul {
  display: flex;
}

.navli {
  margin-right: 1rem;
  display: flex;
  align-items: center;
  font-size: 1.25rem;
}

.brandwrapper {
  display: flex;
  height: 16vh;
  width: 25vw;
  background-color:#034f84;
  justify-content: center;
  align-items: center;
  border-radius: 0px 0 50px 50px;
}

.logo {
  margin-right: 1rem;
  direction: rtl;
  
}

.logotext {
  font-size: 1.25rem;
  color: #a3acb3;
}

.brandtext {
  padding-top: .5rem;
}

.navlinks {
  display: flex;
  flex-wrap: wrap;
  background-color: #034f84;
  height: 9vh;
  border-radius: 0 0 50px 0;
  width: 70vw;
  justify-content: flex-end;
  padding-top: 1rem;
  padding-right: 6rem;
}

.brandwrapper a {
  background-color: #034f84;;
  display: flex;
  height: 16vh;
  width: 25vw;
  border-radius: 0px 0 50px 50px;

}


a {
  padding: 5px 10px 5px 10px;
  /* border-radius: 20px; */
  align-items: center;
  justify-content: center;
  color: white;
  transition: 500ms;
  
}

a:link {
  text-decoration: none;
}

a:hover {
  /* background-color: rgb(255,145,75); */
  color: rgb(67,217,230);
  border-bottom: 1px solid rgb(255,145,75);
}

.active {
  color: rgb(67,217,230);
  border-bottom: 1px solid rgb(255,145,75);
}

.white1 {
  height: 15px;
  width: 90px;
  background-color: rgb(67,217,230);
  border-radius: 3px;
}

.orange {
  height: 15px;
  width: 75px;
  background-color: rgb(255,145,75);
  border-radius: 3px;
  margin-top: 1rem;
}

.white2 {
  height: 15px;
  width: 60px;
  background-color: rgb(67,217,230);
  border-radius: 3px;
  margin-top: 1rem;
}

.white3 {
  height: 15px;
  width: 45px;
  background-color: rgb(67,217,230);
  border-radius: 3px;
  margin-top: 1rem;
}

.hvr-grow-shadow {
  display: inline-block;
  vertical-align: middle;
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -webkit-transition-property: box-shadow, transform;
  transition-property: box-shadow, transform;
  margin-right: 1rem;
  display: flex;
  align-items: center;
  font-size: 1.1rem;
}
.hvr-grow-shadow:hover, .hvr-grow-shadow:focus, .hvr-grow-shadow:active {
  box-shadow: 0 10px 10px -10px rgba(0, 0, 0, 0.5);
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}

.material-icons.md-16 { font-size: 16px; color: rgb(255,145,75);}
.material-icons.md-14 { font-size: 14px; color: rgb(255,145,75);}

@media only screen and (max-width: 1250px) {
  .navlinks {
    flex-wrap: nowrap;
    justify-content:flex-start;
  }
  
  .navli {
    display: flex;
    align-items: center;
   font-size: 1rem;
  }

.material-icons.md-16{
  display: none;
}
}

@media only screen and (max-width: 1024px) {
  
  .navli {
    margin-right: .5rem;
  
    font-size: 14px;
  }
  
  .contact1{
    font-size: 14px;
  }
  
  .logotext{
   font-size: 14px; 
  }
  .material-icons.md-14 {
    display: none;
  }

  .white1 {
    height: 14px;
    width: 65px;
    margin-top: .6rem;
  }
  
  .orange {
    height: 14px;
    width: 55px;
    margin-top: .6rem;
  }
  
  .white2 {
    height: 14px;
    width: 45px;
    margin-top: .6rem;
  }
  
  .white3 {
    height: 14px;
    width: 35px;
    margin-top: .6rem;
  }

}


@media only screen and (max-width: 768px){
  .navli {
    
    margin-right: 0rem;
    font-size: 12px;
  }
  
  .logotext{
    font-size: 10px; 
   }
  
  .white1 {
    height: 12px;
    width: 35px;
    margin-top: .5rem;
    
  }
  
  .orange {
    height: 12px;
    width: 30px;
    margin-top: .5rem;
  }
  
  .white2 {
    height: 12px;
    width: 25px;
    margin-top: .5rem;
  }
  
  .white3 {
    height: 12px;
    width: 20px;
    margin-top: .5rem;
  }

  .contact {
    display: none;
  }

  
}




