.updates{
  width: 80vw;
  text-align: center;
  margin-top: 1rem; 
}

.updates h2 {
  background-color: #034f84;
  display: inline-block;
  padding: .5rem 3rem;
  border-radius: 20px;
  color:white;
  text-align: center;
}

.updateswrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 1rem;
  margin-top: .5rem;  
}

