  margin-top: 1rem; 
}

.careers h2 {
  background-color: #034f84;
  display: inline-block;
  padding: .5rem 3rem;
  border-radius: 20px;
  color:white;
  text-align: center;
}

.careerswrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 1rem;
