.portal{
  width: 80vw;
  text-align: center;
  margin-top: 1rem; 
}

.portal h2 {
  background-color: #034f84;
  display: inline-block;
  padding: .5rem 3rem;
  border-radius: 20px;
  color:white;
  text-align: center;
}

.portalwrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 1rem;
  margin-top: .5rem;  
}

.billing{  
  width: 35vw;
  padding: 1rem;
  margin-right: 2rem;
  margin-bottom: 1rem;
  background-color: rgb(67,217,230);
  border-radius: 10px;
  align-items: center;
  text-align: center;
}

.billing h5{
  padding-bottom: 1rem;
}

.referfriend {
  width: 35vw;
  padding: 1rem;
  margin-right: 2rem;
  background-color: rgb(255,145,75);
  border-radius: 10px;
  margin-bottom: 1rem;
  align-items: center;
  text-align: center;
}



.referfriend h5{
  padding-bottom: 1rem;
}







