footer {
  display: flex;
  background-color: white;
  height: 7vh;
  align-items: center;
  width: 95vw;
  position: fixed; 
  bottom: 0;
  padding-top: 2rem;
  padding-bottom: 2rem;
   
}

.footerbrandtext {
 display: flex;
  background-color: #034f84;
  width: 30vw;
  height: 6vh;
  align-items: center;
  justify-content: flex-start;
  padding-left: 3.5rem;
  border-radius: 50px 0 0 0;
  color: #a3acb3;
}

.copywritesmall{
  display: none;
}
.footul {
  display: flex;
  margin-top: 1rem;
  padding-right: 3.5rem;
  background-color: #034f84;
  border-radius: 0 50px 0 0;
  width: 65vw;
  height: 6vh;
  justify-content: flex-end;
}

li {
  margin-right: 1rem;
  display: flex;
  align-items: center;
  font-size: 1rem;
  
}
a {
  background-color: #034f84;
  padding: 5px 10px 5px 10px;
  align-items: center;
  justify-content: center;
  color: white;
  transition: 500ms
}

a:link {
  text-decoration: none;
}

a:hover {
  color: rgb(67,217,230);
}


span {
  margin-right: 5px;
}
.material-icons.md-14 { font-size: 14px; }

.copywrite {
  padding-top: 1rem;
  font-size: 12px;
}



@media only screen and (max-width: 1320px) {
  
  li {
    
    display: flex;
    align-items: center;
   font-size: 1rem;
    
  }
  a {
    align-items: center;
    justify-content: center;
    color: white;
    transition: 500ms
  }
  .footerbrandtext {
     width: 20vw;
   }
   .footul {
    width: 75vw;
  }
   .copywritesmall {
     display: none;
     padding-top: 1rem;
     font-size: 14px;
   }
  
}

@media only screen and (max-width: 1140px) {
  .copywrite {
    display: none;
  }

  .copywritesmall {
    display: inline;
  }

  .lifontsize {
    font-size: 14px;
  }

  li {
    margin-right: .75rem;
    
  }

  .hideme {
    display:inline-flex
    }
 
}

@media only screen and (max-width: 768px){

  .lifontsize {
    font-size: 10px;
  }
  li {
    margin-right: 0rem;
    
  }
 
  .footul {
    padding-left: 0px;
}

  .footerbrandtext {
    display: none;
  }

  .footul {
    width: 95vw;
    border-radius: 50px 50px 0 0;
    justify-content:center
  }
a {
  border-radius: 50px 50px 0 0;
}

.hideme {
  display: none;
}
}

