.faq {
  width: 80vw;
  text-align: center;
  margin-top: 1rem;  
}

.faq h2 {
  background-color: #034f84;
  display: inline-block;
  padding: .5rem 3rem;
  border-radius: 20px;
  /* margin-bottom: 1rem; */
  color:white;
  text-align: center;
  
}
.faqwrapper {
  width: 80vw;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 1rem;
  margin-top: .5rem;
  
}

.faqtop10 {
  width: 15vw;
  
  padding: 1rem;
  margin-right: 2rem;
  margin-bottom: 2rem;
  background-color: rgb(255,145,75);
  border-radius: 10px;
  
  align-items: center;
  text-align: center;
}
.faqtop10 h4{
  padding-bottom: 1rem;
  border-bottom: 1px solid black;
}
.faqtop10 h5{
  padding-bottom: 1rem;
}

.blue {
  background-color: rgb(67,217,230);
}




